interface HMOCheckboxProps{
    checked?: boolean;
}

const HMOCheckbox: React.FC<HMOCheckboxProps> = (
	props: HMOCheckboxProps,
): JSX.Element => {
    //const classes = hmocheckboxStyles(props);
    const hmocheckboxStyles = {
            size: 12,
            padding: 0,
            paddingRight: 8,
            color: '#272E4C',
            '&$checked': {
                color: '#272E4C',
            },
    };

    if(props.checked){
        return (
            <Checkbox disabled style={hmocheckboxStyles} color="default" {...props} />
        );
    }
    else{
        return (
            <Checkbox disabled style={hmocheckboxStyles} color="default" {...props} />
        );
    }

}


