interface HMOItemProps {
    name?: string;
    value?: string;
    link?: string;
}

const HMOItem: React.FC<HMOItemProps> = (
	props: HMOItemProps,
): JSX.Element => {
    const classes = hmoitemStyles(props);

    return(
        <Grid container alignItems="center" style={{paddingTop: 4, paddingBottom: 4}}>
            <Grid item xs={5}>
                <Typography className={classes.hmoname}>{props.name}</Typography>
            </Grid>
            <Grid item xs={7}>
                {
                    props.link ?
                    <Link to={props.link} className={classes.hmovaluelink}>{props.value}</Link>
                    : <Typography className={classes.hmovalue}>{props.value}</Typography>
                }
            </Grid>
        </Grid>
    )
}

