    getMemberData = () => {
        this.setState({
            loading: true
        });
        API.getMemberData(this.props.match.params.member_id).then(response => {
            console.log('get member data response: ',response);
            this.setState({
                loading: false
            });
            if(response){
                console.log("MEMBER DATA", response);
                let firstName = response && response.first_name ? response.first_name : '';
                let mi = response && response.middle_name ? response.middle_name.charAt(0) + '.' : '';
                let lastName = response && response.last_name ? response.last_name : '';
                this.setState({
                    memberData: response,
                    fullName: firstName+' '+mi+' '+lastName,
                });
            }
            API.getMemberLogs(this.props.match.params.member_id).then(response2 => {
                if(response2 && response2.error === undefined){
                    let memberLogs = response2.map((item: any) => {
                        item['revision_time'] = item.revision_date;
                        return item;
                    });
                    this.setState({
                        memberLogs: memberLogs
                    });
                }

                API.getMemberDocuments(this.props.match.params.member_id).then(response3 => {
                    this.setState({
                        loading: false
                    });
                    if(response3 && response3.error === undefined){
                        this.generateDocumentData(response3, true);
                    }
                });
            });

        });
    }

